/* General grid styles */
.cbp-ig-grid {
	list-style: none;
	padding: 0 0 50px 0;
	margin: 0;
}
/* Clear floats */
.cbp-ig-grid:before, .cbp-ig-grid:after {
	content: " ";
	display: table;
}
.cbp-ig-grid:after {
	clear: both;
}
/* grid item */
.cbp-ig-grid li {
	width: 33%;
	float: left;
	height: 330px;
	text-align: center;
	border-top: 1px solid #767E9E;
}

/* we are using a combination of borders and box shadows to control the grid lines */
.cbp-ig-grid li:nth-child(-n+3) {
 border-top: none;
}
 .cbp-ig-grid li:nth-child(3n-1), .cbp-ig-grid li:nth-child(3n-2) {
 box-shadow: 1px 0 0 #767E9E;
}
/* anchor style */
.cbp-ig-grid li > a {
	display: block;
	height: 100%;
	color: #767E9E;
	transition: background 0.2s;
}
/* title element */
.cbp-ig-grid .cbp-ig-title {
	font-family: aller_lightregular;
	font-size: 1.5em;
	margin: 20px 0 10px 0;
	padding: 20px 0 0 0;
	position: relative;
	text-transform: uppercase;
	transition: transform 0.2s;
}
.cbp-ig-grid .cbp-ig-title:before {
	content: '';
	position: absolute;
	background: #767E9E;
	width: 160px;
	height: 6px;
	top: 0px;
	left: 50%;
	margin: -10px 0 0 -80px; /* top or translate does not seem to work in Firefox */
	transition: margin-top 0.2s;
}
.cbp-ig-grid .cbp-ig-category {
	text-transform: uppercase;
	display: inline-block;
	font-size: 1em;
	letter-spacing: 1px;
	color: #AFB6D1;
	transform: translateY(10px);
	opacity: 0;
	-webkit-transition: -webkit-transform 0.3s, opacity 0.2s;
	-moz-transition: -moz-transform 0.3s, opacity 0.2s;
	-webkit-transition: transform 0.3s, opacity 0.2s;
}
.cbp-ig-grid li:hover .cbp-ig-category, .touch .cbp-ig-grid li .cbp-ig-category {
	opacity: 1;
	transform: translateY(0px);
}
/* Hover styles */

.cbp-ig-grid li > a:hover {
	background: #353947;
}
.cbp-ig-grid li > a:hover .cbp-ig-icon {
	transform: translateY(10px);
}
.cbp-ig-grid li > a:hover .cbp-ig-icon:before, .cbp-ig-grid li > a:hover .cbp-ig-title {
	color: #AFB6D1;
}
.cbp-ig-grid li > a:hover .cbp-ig-title {
	transform: translateY(-30px);
}
.cbp-ig-grid li > a:hover .cbp-ig-title:before {
	background: #AFB6D1;
	margin-top: 60px;
}
