}
/* Direction Nav */
.flex-direction-nav a {
	/* background: url("../img/bg_direction_nav.png") no-repeat scroll 0 0 #303441; */
	cursor: pointer;
	display: block;
	height: 47px;
	margin: -20px 0 0;
	opacity: 0;
	position: absolute;
	text-indent: -9999px;
	top: 50%;
	width: 44px;
}
.flex-direction-nav .flex-next {
	background-position: 100% 0;
	right: 0;
}
.flex-direction-nav .flex-prev {
	left: 0;
}
.flexslider:hover .flex-next {
	opacity: 0.8;
	right: 0;
}
.flexslider:hover .flex-prev {
	opacity: 0.8;
	left: 0;
}
.flexslider:hover .flex-next:hover, .flexslider:hover .flex-prev:hover {
	opacity: 1;
}
.flex-direction-nav .disabled {
	opacity: .3!important;
	filter:alpha(opacity=30);
	cursor: default;
}
/* Control Nav */
.flex-control-nav {
	width: 100%;
	position: absolute;
	bottom: -40px;
	text-align: center;
	display: none;
}
.flex-control-nav li {
	margin: 0 6px;
	display: inline-block;
	zoom: 1;
*display: inline;
}
